<template>
  <div id="app">
    <main>
      <router-view/>
    </main>
    <nav class="NavBar">
      <nav-bar></nav-bar>
    </nav>
  </div>
</template>
<script>
  import NavBar from "@/components/NavBar"
  export default {
    components:{
      NavBar
    },
    data(){
      return{
        rem:750
      }
    },
    mounted(){
      this.setRem()
      window.addEventListener("resize",()=>{
        this.setRem()
      })
    },
    methods:{
      setRem(){
        document.querySelector("html").style.fontSize 
        = window.innerWidth / this.rem + 'px'
      }
    }
  }
</script>

