import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path:'',
    redirect:{path:'/home'}
  },
  {
    path:'/home',
    name:'home',
    meta:{
      name:'首页'
    },
    component:()=>import('@/views/Home')
    // component:()=>import( /* webpackChunkName: 'a1' */'@/views/Home')
  },
  {
    path:'/indent',
    name:'indent',
    meta:{
      name:'点菜'
    },
    component:()=>import('@/views/Indent')
  },
  {
    path:'/order',
    name:'order',
    meta:{
      name:'订单'
    },
    component:()=>import(/* webpackChunkName: 'showOne' */'@/views/Order')
  },
  {
    path:'/mine',
    name:'mine',
    meta:{
      name:'我的'
    },
    component:()=>import(/* webpackChunkName: 'showOne' */'@/views/Mine')
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
