import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import { Lazyload } from 'mint-ui';
import { Table,Loading } from 'element-ui';
Vue.use(Lazyload);
Vue.config.productionTip = false
Vue.use(Table);
Vue.use(Loading.directive);
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
